.cont {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #222; /* Navbar background color */
  padding: 10px 20px; /* Adjust padding */
  height: 60px; /* Adjust the Navbar height */
}

.logo {
  color: white; /* Text color for the logo */
  text-decoration: none; /* Remove underline for the logo */
  font-size: 24px; /* Adjust font size for the logo */
}

.logo-link {
  text-decoration: none; /* Remove underline for the logo link */
}

.menu {
  display: flex;
  align-items: center;
}

.nav_menu {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.nav_menu a {
  text-decoration: none;
  color: #fff; /* Text color for links */
  margin-right: 20px; /* Adjust spacing between links */
  transition: border-bottom 0.3s ease; /* Add underline transition */
}

.nav_menu a:hover {
  border-bottom: 2px solid #ff6600;
}

.nav_togg {
  cursor: pointer;
  display: none; /* Hide hamburger menu by default */
}

.togg {
  background-color: transparent;
}

.item1,
.item2,
.item3 {
  background-color: #fff; /* Background color for menu icon items */
  height: 3px;
  width: 30px;
  margin: 2px 0;
  transition: background-color 0.3s ease;
}

/* Show hamburger menu on smaller screens */
@media (max-width: 768px) {
  .nav_togg {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 24px;
    width: 30px;
  }
}
