/* CSS for the Home component */
.home-container {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 100vw;
  height: 100vh;
}

.box {
  color: black;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  width: 80vw;
  max-width: 800px;
  margin: 0 auto;
}

.heading {
  font-size: 35px;
  margin-top: -10px;
  margin-bottom: 20px;
  text-transform: uppercase;
  transition: color 0.3s ease; /* Add transition for text color change */
  cursor: pointer;
}

/* Heading hover effect with border */
.heading:hover {
  color: white; /* Change text color to white on hover */
  -webkit-text-stroke: 2px black; /* Add a 1px black border around the text */
}

.banner-image {
  max-width: 100%;
  display: block;
  border-radius: 20px;
}
